import React, { useState, useEffect } from 'react';
import { useDataContext } from '../contexts/DataContext';
import BaseURL, { IMAGE_BASE_URL } from '../components/ApiCreds';
import { useNavigate } from 'react-router-dom'; // Use react-router for navigation
import { getToken } from '../utils/functions';
import BottomNavigation from '../components/BottomNavigation';
import styled from 'styled-components'; // Import Styled Components

const SafeArea = styled.div`
  flex: 1;
`;

const MainView = styled.div`
  flex: 1;
  padding: 0 20px;
`;

const TopRow = styled.div`
  display: flex;
  flex-direction: row;
  align-items: center;
  margin-top: 30px;
`;

const Avatar = styled.img`
  height: 48px;
  width: 48px;
  border-radius: 50%;
  background-color: #FFE5BB;
`;

const UserInfo = styled.div`
  margin-left: 10px;
`;

const BaseText = styled.span`
  color: #111920;
`;

const BaseUserName = styled.span`
  font-size: 16px;
  font-weight: 500;
`;

const CardContainerWrapper = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  gap: 20px;
  flex-wrap: wrap;
`;

const CardContainer = styled.div`
  display: flex;
    
    align-items: center;
    justify-content: space-between;
  width: 100%;
  text-align: center;
  cursor: pointer;
`;

const TextView = styled.div`
  margin-bottom: 10px;
`;

const AssessmentImage = styled.img`
  width: 80px;
  height: 80px;
`;

const Heading = styled.h3`
  font-size: 20px;
  font-weight: 500;
`;

const Para = styled.p`
  font-size: 14px;
  font-weight: 400;
`;

const Notes = styled.div`
  color: black;
  margin-top: 30px;
  font-size: 16px;
`;

const Text = styled.div`
  color: #00000080;
  margin-top: 10px;
  font-weight: 400;
  font-size: 15px;
`;

function HomePage() {
  const { userId } = useDataContext();
  const [userData, setUserData] = useState(null);
  const [avatarUrl, setAvatarUrl] = useState(null);
  const { updateUserDetail } = useDataContext();
  const history = useNavigate(); // For navigation

  const User = () => localStorage.getItem("userId");
  const [userDetail, setUserDetail] = useState(null);

  useEffect(() => {
    try {
      const storedUserDetail = localStorage.getItem("userDetails");
      if (storedUserDetail) {
        setUserDetail(JSON.parse(storedUserDetail)); // Parse the data and set it in state
      }
    } catch (error) {
      console.error("Error retrieving or parsing userDetails from localStorage", error);
    }
  }, []);

  const fetchData = async () => {
    const token = await getToken();
    const UserId = await User();
    console.log(UserId);
    try {
      const response = await fetch(`${BaseURL}/get_user_profile/${UserId}`, {
        headers: {
          'Authorization': 'Bearer ' + token,
        },
      });
      const userData = await response.json();
      console.log(userData);
      updateUserDetail(userData);
      // Save user data in state
      setUserData(userData);

      // Fetch avatar URL if userData contains AvatarId
      if (userData?.AvatarID) {
        const avatarId = userData.AvatarID; // Use optional chaining to safely access AvatarId
        const response = await fetch(`${BaseURL}/get_avatar/${avatarId}`, {
          headers: { 'Authorization': 'Bearer ' + token },
        });
        const avatarData = await response.json();
        if (avatarData && avatarData.AvatarURL) {
          updateUserDetail({ avatarUrl: `${BaseURL}${avatarData.AvatarURL}` });
          setAvatarUrl(avatarData.AvatarURL);
        }
      }
    } catch (error) {
      // Handle error
    }
  };

  useEffect(() => {
    fetchData();
  }, []);

  const avatarUrlNew = `${IMAGE_BASE_URL}${avatarUrl}`;

  if (!userData) {
    return <div>Loading...</div>;
  }

  const onPressTab = (route) => {
    console.log(userDetail?.SubscriptionDetails);
    if (userDetail?.SubscriptionDetails) {
      history(`/${route}`); // Use react-router for navigation
    } else {
      alert('Subscription', 'Please subscribe first');
    }
  };

  return (
    <SafeArea>
      <MainView>
        <TopRow>
          <Avatar
            src={userDetail?.avatarUrl || avatarUrlNew}
            alt="Avatar"
          />
          <UserInfo>
            <BaseText>Logged in as </BaseText>
            <BaseUserName>{userData.FullName}</BaseUserName>
          </UserInfo>
        </TopRow>

        <CardContainerWrapper>
          <CardContainer onClick={() => onPressTab('assessmentPage')}>
            <TextView>
              <Heading>Assessment</Heading>
              </TextView>
               <Para>Test your speech-language skills with our AI-based assessments</Para>
           
            <AssessmentImage
              src={require('../assets/images/home1.png')}
              alt="Assessment"
            />
          </CardContainer>

          <CardContainer onClick={() => onPressTab('exercisePage')}>
            <TextView>
              <Heading>Exercises</Heading>
            </TextView>
              <Para>Improve your speaking skills with our AI Therapist</Para>
            <AssessmentImage
              src={require('../assets/images/home2.png')}
              alt="Exercises"
            />
          </CardContainer>

          <CardContainer onClick={() => onPressTab('reportsPage')}>
            <TextView>
              <Heading>Reports</Heading>
            </TextView>
              <Para>View your progress and improvement</Para>
            <AssessmentImage
              src={require('../assets/images/home3.png')}
              alt="Reports"
            />
          </CardContainer>
        </CardContainerWrapper>

        <Notes>
          <h3>Notes :</h3>
          <ul>
            <li>Please do not switch/change between the screens while using IzzyAl. Doing so may disable the Record button.</li>
            <li>Adjust your face image within the camera frame for proper facial recording.</li>
            <li>Be close and within the frame.</li>
            <li>Press the Record button to start and stop the audio recording.</li>
            <li>Press the Previous button to go back and record again.</li>
            <li>Press End/Finish at any point to exit the assessment or exercise.</li>
            <li>If you close the Assessment, Exercise, or Game without clicking Finish/End, your report will not be generated.</li>
          </ul>
        </Notes>
      </MainView>
      <BottomNavigation />
    </SafeArea>
  );
}

export default HomePage;
